import React from "react"

const SingleColor = ({ color, onClick, isActive, isVisible, index }) => {
  const handleActiveUI = () => onClick(color, index)

  return (
    <Div
      border="2px solid"
      borderColor={isActive ? "black" : "transparent"}
      bg={color}
      height={32}
      width={48}
      style={{
        float: 'left',
        cursor: "pointer",
        position: "relative",
        boxShadow: isVisible ? " 0 0 10px rgba(0,0,0,0.3)" : "none",
        transform: isVisible ? "scale(1.25) rotate(15deg)" : "none",
        zIndex: isVisible ? 4 : 1,
        transition: "transform 200ms ease-in-out",
      }}
      onClick={handleActiveUI}
    />
  )
}

const Palette = ({
  palette,
  onClick,
  activeColors,
  onAddColor,
  pickerColor
}) => {
  return (
    <Div css={{
      overflow: 'hidden',
      ':after': {content: "", display: 'table', clear: 'both'}
    }}>
      {palette.map((color, i) => (
        <SingleColor
          isActive={i === pickerColor.index}
          isVisible={activeColors.includes(color)}
          key={i}
          index={i}
          color={color}
          onClick={onClick}
        />
      ))}
      <AddColor style={{float: 'left' }} mt={1} ml={1} onAddColor={onAddColor} />
    </Div>
  )
}

export default Palette
